
/**
 * Awesome Grid
 */

// Vars

@grid-fixed-width: 1140px;
@grid-fixed-gutter: 100px;
@grid-max-cols: 12;


// Calculations

@grid-fluid-gutter: percentage((100 / (@grid-fixed-width / @grid-fixed-gutter)) / 100);
@grid-one-col: (100% - (@grid-fluid-gutter * (@grid-max-cols))) / @grid-max-cols;


