// Mixins

.row() {
	.cf();
	margin: 0 -@grid-fluid-gutter/2;
//	include _debug('row', @grid-max-cols);
}

.col(@grid-max-cols: 12) {
	margin: 0 @grid-fluid-gutter/2;
	float: left;
	width: (@grid-one-col * @grid-max-cols) + (@grid-fluid-gutter * (@grid-max-cols - 1));
//	include _debug('col', @grid-max-cols);
}

.push(@grid-max-cols: 1) {
	margin-left: (@grid-one-col * @grid-max-cols) + (@grid-fluid-gutter * (@grid-max-cols + 1)) !important;
//	include _debug('push', @grid-max-cols);
}

.pull(@grid-max-cols: 1) {
	margin-left: (@grid-one-col * @grid-max-cols) + (@grid-fluid-gutter * (@grid-max-cols - 1));
//	include _debug('pull', @grid-max-cols);
}


// Classes

.row {
	.row();
}

	.col {
		.col();
	}


